.attachment-container {
    /* margin-top: -2%; */
    font-family: 'Poppins', sans-serif;
}



.attachment-label {
    display: inline-block;
    color: #003DA4;
    border-radius: 20px;
    cursor: pointer;
    padding: 10px 15px;
    transition: color 0.3s, padding 0.5s, background-color 0.3s;
}

.attachment-label:hover {
    color: #fff; 
    background-color: #FF3C2C; 
    padding: 10px 19px;
}

.attachment-input {
    display: none;
}

.selected-file {
    font-size: 14px;
    color: #333;
}

